//Creating Redux store - createStore
// Logger middle ware include

import { createStore, applyMiddleware, $CombinedState } from 'redux' // to Apply Middle ware
import thunk from "redux-thunk"
import logger from 'redux-logger'
import cakeReducer from './Cake/CakeReducer' // Here One reducer
import RootReducer from './RootReducer' //All Reducers include i.e Rootreducer

const store = createStore(RootReducer, applyMiddleware(logger, thunk))

export default store


