import React, { useEffect, useState, useContext } from 'react'
import { SingleCoin } from '../../Config/api'
import { Criipto } from '../Criptocontext'
import axios from 'axios'
import { useParams } from "react-router-dom"
import CoinInfo from './CoinInfo'
import { LinearProgress } from '@material-ui/core'
import Footer from './Footer'


function Coinpage() {
    const { id } = useParams()
    const { currency, symbol } = useContext(Criipto)
    const [coin, setCoins] = useState()
    const [loading, setloading] = useState(false)

    const fetchCoinData = async () => {
        setloading(true)
        const { data } = await axios.get(SingleCoin(id))
        setCoins(data)
        setloading(false)
        console.log(data)
    }

    useEffect(() => {
        fetchCoinData()
    }, [])

    //console.log(coin.name)

    return (
        <>
        <div style={{ display: 'flex', flexFlow: 'row', background: '#000', width: '100%', height: '100vh', padding: '20px' }}>
            <div style={{ display: 'flex', flexFlow: 'column', width: '30%', borderRight: '2px solid darkgrey', height: '100%', color: '#fff', paddingTop: '80px' }}>
                {
                    loading ? (<LinearProgress style={{ background: 'gold' }} />) : (<><img src={coin?.image.large} alt={coin?.name} style={{ objectFit: 'contain', width: '80%', alignSelf: 'center' }} />
                        <span style={{ fontSize: '2.5vw', fontWeight: 800, alignSelf: 'center' }}>{coin?.name}</span>
                        <span style={{ color: 'darkgrey', marginTop: '20px' }}>
                            {coin?.description.en.split('.')[0]}{'.'}
                        </span>
                        <span style={{ display: 'flex', flexFlow: 'row', marginTop: '20px' }}><h3>Rank: &nbsp;</h3><span style={{ fontSize: '2vw' }}>{'    '}{coin?.coingecko_rank}</span></span>

                        <span style={{ display: 'flex', flexFlow: 'row', marginTop: '20px' }}><h3>Current Price: &nbsp;</h3><span style={{ fontSize: '2vw' }}>{symbol} {coin?.market_data.current_price[currency.toLowerCase()]}</span></span>

                        <span style={{ display: 'flex', flexFlow: 'row', marginTop: '20px' }}><h3>Market Cap: &nbsp;</h3><span style={{ fontSize: '2vw' }}>{symbol}{" "}{coin?.market_data.market_cap[currency.toLowerCase()].toString().slice(0, -6)}M</span></span></>)
                }
            </div>
            <div style={{ width: '70%', color: '#fff', paddingTop: '80px' }}>
                <CoinInfo/>
            </div>
        </div>
        <Footer/>
        </>
    )
}

export default Coinpage
