import React, { Component, createRef} from 'react'
import Ref from './Ref'

class FancyButton extends Component {
    constructor(props) {
        super(props);

        this.compRef = createRef()
    }

    clickHandler = () => {
        this.compRef.current.focus()
       // console.log(this.compRef.current.inputRef.current.value)
    }
    render() {
        return (
            <div>
                <Ref ref={this.compRef} />
                <button onClick={this.clickHandler}>Set Focus</button>
            </div>
        )
    }
}

export default FancyButton
