import React, { Component } from 'react'
import { UserConsumer } from './ContextMain'


export class ContextB extends Component {
    render() {
        //this will return a function with values
        return (
            <UserConsumer>
                {userName => {
                    return <h2>Hello {userName}</h2>
                }}
            </UserConsumer>

        )
    }
}

export default ContextB
