import React, { useState, useEffect, useRef } from 'react'
import AlbumPage from './AlbumPage'
import AvatarPage from './AvatarPage'
import LocationPage from './LocationPage'
import MarchindisePage from './MarchindisePage'


function NewPage() {

    let init = [{
        id: 0,
        name: 'Avatar'
    },
    {
        id: 1,
        name: 'Merchandise'
    },
    {
        id: 2,
        name: 'Location'
    },
    {
        id: 3,
        name: 'Album'
    }]

    const [tabs, settabs] = useState(init)
    const [title, settitle] = useState('Avatar')

    const childAvtARef = useRef(null)
    const childAlbumRef = useRef(null)
    const childLocRef = useRef(null)
    const childMarRef = useRef(null)

    let tabData = []


    useEffect(() => {
        childAvtARef.current.style.display = 'flex'
    }, [])



    const viewTab = (i) => {
        settitle(i.name)
        let pages = [childAvtARef, childMarRef, childLocRef, childAlbumRef]
        pages.forEach((i) => {
            i.current.style.display = 'none'
        })
        pages[i.id].current.style.display = 'flex'
    }



    let result = tabs.map((data, index) => {
        return <label htmlFor="tab1" className="tablinks" id={index} onClick={() => viewTab(data)} key={index}><img src={'images/' + data.name + '.png'} alt={data.name} className="tab_icon" /><i className="fal fa-angle-double-right fa-3x tab_arrow" style={{ paddingLeft: "10px" }}></i></label>
    })

    tabData.push(result)

    console.log("Home page")
    return (
        <section>
            <div className='main-container'>
                <div className='tab'>
                    <div className="tab_title">
                        <h3 id="title" className="sub-title-div">{title}</h3>
                    </div>
                    <div className="tabs-container" id='tabContent'>
                        <ul>{tabData}</ul>
                    </div>
                </div>
                <div id="tabContainer">
                    <AvatarPage avtRef={childAvtARef} />
                    <MarchindisePage marRef={childMarRef} />
                    <LocationPage locRef={childLocRef} />
                    <AlbumPage albumRef={childAlbumRef} />
                </div>
            </div>
        </section>
    )
}

export default NewPage
