import React from 'react'
import { connect } from 'react-redux'
import { buyCake } from './Cake/CakeAction'
import { buyIcecream } from './Icecream/IcecreamAction'

function ItemContainer(props) {
    return (
        <div>
            <h2>Items - {props.items}</h2>
            <button onClick={props.dispatchItem}>Buy Items</button>
        </div>
    )
}

const StateMapitems = (state, propsOwn) =>{
    const itemsview = propsOwn.cakes ? state.cake.numOfCakes : state.iceCream.numOfIcecream
    return{
        items:itemsview
    }
}

const DispatchMapItem = (dispatch, propsOwn) =>{
    const dispathchItem = propsOwn.cakes ? () => dispatch(buyCake()) : () => dispatch(buyIcecream())

    return{
        dispatchItem : dispathchItem
    }
}

export default connect(StateMapitems, DispatchMapItem)(ItemContainer)

