import React, { Component } from 'react'

const HocFunctionality = OrginalComp => {
    class newComponent extends Component {
        constructor(props) {
            super(props);
            this.state = {
                count: 0
            }
        }

        incrementHandler = () => {
            this.setState(preV => {
                return { count: preV.count + 1 }
            })
        }

        render() {
            return <OrginalComp count={this.state.count} incrementHandler={this.incrementHandler} {...this.props} />
        }
    }

    return newComponent

}

export default HocFunctionality
