import React, { Component, PureComponent } from 'react'

export class PureCompTest extends Component { // PureComponent renders when state has changes , component Changes always with setinterval 
    constructor(props) {
        super(props);
        this.state = {
            counter: 0
        }

        let intId = ''

    }

    static getDerivedStateFromProps(props, state) {
        //clearInterval(this.intId)
        //console.log("getDerivedStateFromProps" + props, state)
        return state
    }

    componentDidMount() {
        this.intId = setInterval(() => {
            this.setState({ counter: 0 })
        }, 2000);
    }



    clearInt = () => {
        clearInterval(this.intId)
    }




    render() {
        console.log('Render')
        return (
            <div>
                {this.state.counter}
                <button onClick={this.clearInt}>Clear</button>
            </div>
        )
    }
}

export default PureCompTest
