import React, { Component } from 'react'
import HocFunctionality from './HocFunctionality'

class HocTwo extends Component {
    constructor(props) {
        super(props);
        
    }
    
    render() {
        const {count, incrementHandler, myName} = this.props
        return (
            <div>
                <button onMouseOver={incrementHandler}>{myName} Increment</button>
                Count: {count}
            </div>
        )
    }
}

export default HocFunctionality(HocTwo)
