import { ERROR_USER_DATA, RETRIVE_USER_DATA, SUCCESS_USER_DATA } from "./AsyncType"



export const retriveUserData = () => {
    return {
        type: RETRIVE_USER_DATA
    }
}

export const successUserData = (users) => {
    return {
        type: SUCCESS_USER_DATA,
        uData: users
    }
}

export const errorUserData = (error) => {
    return {
        type: ERROR_USER_DATA,
        uData: error
    }
}
