import React, { Component } from 'react'
import HocFunctionality from './HocFunctionality'


class HocOne extends Component {
    constructor(props) {
        super(props);

    }

    render() {
        const { count, incrementHandler, myName } = this.props
        return (
            <div>

                <button onClick={incrementHandler}>{myName} Increment</button>
                Count: {count}
            </div>
        )
    }
}

export default HocFunctionality(HocOne)
