import React, {useState} from 'react'
import { connect } from 'react-redux'// this is to connect to multiple mapprops
import { buyIcecream } from '../Redux'

function IceCreamShop(props) {
    const [number, setnumber] = useState(1)
    return (
        <div>
            <h2>No of Icecreams - {props.numOfIcecream}</h2>
            <input type="text" value={number} onChange={e => setnumber(e.target.value)} />
            <button onClick={() => props.buyIcecream(number)}>Buy - Icecream</button>
        </div >
    )
}

const mapStateProps = (state) => {
    return {
        numOfIcecream: state.iceCream.numOfIcecream
    }
}

const mapDispatchProps = (dispatch) => {
    return {
        buyIcecream: (number) => dispatch(buyIcecream(number))
    }
}

export default connect(mapStateProps, mapDispatchProps)(IceCreamShop)
