import React, { useContext, useState, useEffect } from 'react'
import { Container, LinearProgress, Table, TableContainer, TextField, Typography, TableCell, TableRow, TableHead, TableBody, TablePagination } from '@material-ui/core'
import Pagination from '@mui/material/Pagination';
import { createStyles, ThemeProvider } from '@material-ui/styles'
import axios from 'axios'
import { useNavigate } from 'react-router'
import { CoinList } from '../../../Config/api'
import { Criipto } from '../../Criptocontext'
import { numberWithComa } from '../Banner/Carousel'


export default function CoinsTables() {
    const { currency, symbol } = useContext(Criipto)
    const [coins, setcoins] = useState([])
    const [loading, setloading] = useState(false)
    const [search, setsearch] = useState([])
    let navigate = useNavigate()
    const [page, setpage] = useState(1)
    const useStyle = createStyles(() => ({
        row: {
            background: '#16171a',
            cursor: 'pointer',
            "&hover": {
                background: '#131111'
            }
        },
        pagination: {
            "& .MuiPagination-root": {
                color: 'gold'
            }
        },
        inputbox: {
            "& .MuiInputBase-input": {
                borderColor: '#fff',
                color: 'white',
                background: 'white'
            },

        }
    }))

    const classes = useStyle()

    const coinTable = async () => {
        setloading(true)
        let { data } = await axios.get(CoinList(currency))
        setcoins(data)
        //console.log(data)
        setloading(false)
    }



    useEffect(() => {
        coinTable()
    }, [currency])

    const handleSearch = () => {
        return coins.filter(coin => coin.name.toLowerCase().includes(search) || coin.symbol.toLowerCase().includes(search))
    }
    //console.log(handleSearch())

    return (
        <ThemeProvider>
            <Container style={{ textAlign: 'center', height: '100%' }}>
                <Typography variant="h4" style={{ marginTop: '20px', marginBottom: '20px' }}>
                    Crypto Market View
                </Typography>
                <TextField label="Search" variant="outlined" style={{ marginBottom: '20px', width: '100%', background:'darkgrey'}} onChange={(e) => setsearch(e.target.value)} />

                <TableContainer>
                    {
                        loading ? (<LinearProgress style={{ background: 'gold' }} />) : (
                            <Table>
                                <TableHead style={{ background: 'gold' }}>
                                    <TableRow>
                                        {["Coin", "Price", "24h Change", "Market Cap"].map((head) => {
                                            return <TableCell style={{ color: "black", fontWeight: '500' }} key={head} align={head === 'Coin' ? '' : 'right'}>
                                                {head}
                                            </TableCell>
                                        })}
                                    </TableRow>
                                </TableHead>
                                <TableBody>{handleSearch().slice((page - 1) * 10, (page - 1) * 10 + 10).map((row) => {

                                    const profit = row.price_change_percentage_24h > 0;
                                    return <TableRow onClick={() => navigate(`/coins/${row.id}`)} className={classes.row} key={row.name}>
                                        <TableCell align="left" scope="row" style={{ display: 'flex', gap: '10', color: '#fff', justifyContent: 'center', flexFlow: 'row' }}>
                                            <img src={row?.image} alt={row.name} style={{ marginBottom: "5px", height: '50px', alignSelf: 'left' }} />
                                            <div style={{ display: 'flex', flexDirection: 'column', justifyContent:'center', textAlign:'left', alignItems:'flex-start', width:'100%' }}><span style={{ fontSizes: '22px', textTransform: 'uppercase' }}>{row.symbol}</span><span style={{ fontSize: '14px' }}>{row.name}</span></div>
                                        </TableCell>
                                        <TableCell align="right" style={{ color: profit ? 'green' : 'red' }}>
                                            {symbol}{''}
                                            {numberWithComa(row.current_price.toFixed(2))}
                                        </TableCell>
                                        <TableCell align="right" style={{ color: 'white' }}>
                                            {profit && '++'}
                                            {row.price_change_percentage_24h.toFixed(2)}
                                        </TableCell>
                                        <TableCell align="right" style={{ color: 'white' }}>
                                            {symbol}{''}
                                            {numberWithComa(row.market_cap.toString().slice(0, -6))}{'M'}
                                        </TableCell>
                                    </TableRow>
                                })}
                                </TableBody>
                            </Table>
                        )
                    }
                </TableContainer>
                <Pagination align='center' style={{padding: '20px', display: 'flex', alignSelf: 'center', align: 'center', justifyContent: 'center', color: 'gold' }} count={(handleSearch()?.length / 10).toFixed(0)} className={classes.pagination} onChange={(_, value) => {
                    setpage(value)
                    window.scroll(0, 450)
                }} />
            </Container>
        </ThemeProvider>
    )
}
