import React, { Component } from 'react'
import LifeCycleB from './LifeCycleB';

class LifeCycle extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: 'Srinivas'
        }

        console.log("1 - Constructor - A") // Loading  1
    }

    static getDerivedStateFromProps(props, state) {
        console.log('2 Af-1 - getDerivedStateFromProps - A') //Loading 2 //AfterClick 1
        return null
    }

    componentDidMount() {
        console.log('7 - componentDidMount -A'); // Loading 7
    }


    shouldComponentUpdate() {
        console.log('AF -2 - shouldComponentUpdate - A');//After click 2
        return true
    }

    getSnapshotBeforeUpdate(prevProp, prevState) {
        console.log('AF -7 - getSnapshotBeforeUpdate - A'); //After click 7
        return null
    }

    componentDidUpdate() {
        console.log('AF- 9 - componentDidUpdate - A'); //Afterclick 9
    }

    clickHandler = () => {
        this.setState({ name: "Samanvita" }) //Click 1
        console.log("Click")
    }

    render() {
        console.log("3 - AF-3 - Render - A") //Loading 3 //After click 3
        return (
            <div>
                <LifeCycleB name={this.state.name} />
                <button onClick={this.clickHandler}>Click {this.state.name}</button>
            </div>
        )
    }
}

export default LifeCycle
