import React, { Component } from 'react'
import Counter from './Counter'
import NavPage from './NavPage'

export class Counters extends Component {
    state = {
        counters: [
            { id: 1, value: 0 },
            { id: 2, value: 0 },
            { id: 3, value: 0 },
            { id: 4, value: 0 },
            { id: 5, value: 0 }
        ]
    }

    incrementhandler = (counterId) => {
        const inCrement = counterId.value++
        this.setState({ inCrement })
    }

    deleteHandler = (counterId) => {
        const counters = this.state.counters.filter(d => d.id !== counterId)
        this.setState({ counters: counters })
    }

    resetHandler = () => {
        const counters = this.state.counters.map(r => {
            r.value = 0
            return r
        })

        this.setState({ counters })
    }

    render() {
        return (
            <React.Fragment>
                <NavPage totalCounter= {this.state.counters.filter(p => p.value > 0).length} />
                <div>
                    <button type="button" className="btn btn-primary btn-sm" onClick={this.resetHandler}><h2>Reset</h2></button>
                    <br /><br />
                    {
                        this.state.counters.map((counter) =>
                            /* To control attributes values like value={counter.value} isSelected={true} id{counter.id} we can use one attribute insted different attributes and values. like above insted we can use one attribute as counter={counter} and in its child we need to add 'counter' before all its values*/
                            <Counter
                                key={counter.id}
                                /*value={counter.value} 
                                isSelected={true} 
                                id={counter.id} */
                                onDelete={this.deleteHandler}
                                onReset={this.resetHandler}
                                handleIncrement={this.incrementhandler}
                                counter={counter}
                            />)
                    }
                </div>
            </React.Fragment>
        )
    }
}

export default Counters
