import React from 'react'
import { createContext, useReducer } from 'react'
import CompAchild from './CompAchild'
import CompBchild from './CompBchild'

const init = {
    first: 0,
    second: 10
}

const reducer = (state, action) => {
    switch (action.type) {
        case 'increment':
            return { ...state, first: state.first + action.val }
        case 'decrement':
            return { ...state, first: state.first - action.val }
        case 'increment2':
            return { ...state, second: state.second + action.val }
        case 'decrement2':
            return { ...state, second: state.second - action.val }
        case 'reset':
            return init
        case 'reset2':
            return init
        default:
            return state
    }
}

export const MyTestDispatch = createContext()

function UseRedUseCont() {
    const [count, dispatch] = useReducer(reducer, init)
    const [count2, dispatch2] = useReducer(reducer, init)
    return (
        <>
            <h1>Counters -  {count.first}</h1>
            <h1>Counters -  {count2.second}</h1>
            <MyTestDispatch.Provider value={{ count, dispatch, count2, dispatch2 }}>
                <CompAchild />
                <CompBchild />
            </MyTestDispatch.Provider>
        </>
    )
}

export default UseRedUseCont
