import { Link, makeStyles } from '@material-ui/core'
import React, { useEffect, useState, useContext } from 'react'
import axios from 'axios'
import { TrendingCoins } from '../../../Config/api'
import { Criipto } from '../../Criptocontext'
import AliceCarousel from 'react-alice-carousel'
import { useNavigate } from 'react-router'


const useStyle = makeStyles((theme) => ({
    carousel: {
        height: '50%',
        display: 'flex',
        justifyContent: 'center'
    },
    caroselItem: {
        display: 'flex',
        flexDirection: 'column',
        textTransform: 'uppercase',
        justifyContent: 'center',
        alignItems: 'center',
        color: 'white'
    }
}))

export const numberWithComa = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+ (?!\d))/g, ",");
}

function Carousel() {
    const navigate = useNavigate()
    const context = useContext(Criipto)
    const { currency, symbol } = context
    const classes = useStyle()
    const [trending, settrending] = useState([])

    const fetchTrendCoins = async () => {
        const { data } = await axios.get(TrendingCoins(currency))
        settrending(data)
    }


    //console.log(trending)
    useEffect(() => {
        fetchTrendCoins()
    }, [currency])

    const items = trending.map((coin, y) => {
        let profit = coin.price_change_percentage_24h >= 0
        return (
            <Link href={`/coins/${coin.id}`} className={classes.caroselItem}>
                <img src={coin?.image} alt={coin.name} style={{ marginBottom: '10px', height: '50px' }} />

                <span>
                    {coin?.symbol} &nbsp;
                    <span>{profit && '+'}{coin?.price_change_percentage_24h?.toFixed(2)} %</span>
                </span><br />
                <span style={{ fontSize: 22, fontWeight: 500 }}>
                    {symbol} {numberWithComa(coin?.current_price.toFixed(2))}
                </span>
            </Link>
        )
    })

    const responsive = {
        0: {
            items: 2
        },
        512: {
            items: 4
        }
    }


    return (
        <div className={classes.carousel}>
            <AliceCarousel
                mouseTracking
                infinite
                autoPlayInterval={1000}
                animationDuration={1500}
                disableDotsControls
                responsive={responsive}
                autoPlay
                items={items}
                disableButtonsControls
            />
        </div>
    )
}

export default Carousel
