import { ERROR_USER_DATA, RETRIVE_USER_DATA, SUCCESS_USER_DATA } from "./AsyncType"

const userData = {
    loading: false,
    users: [],
    error: ''
}

const AsyncUserReducer = (state = userData, action) => {
    switch (action.type) {
        case RETRIVE_USER_DATA: return {
            ...state,
            loading: true
        }
        case SUCCESS_USER_DATA: return {
            loading: false,
            users: action.uData,
            error: ''
        }
        case ERROR_USER_DATA: return {
            loading: false,
            users: [],
            error: action.uData
        }
        default: return state
    }
}
export default AsyncUserReducer