import React, { createElement, useState, useRef, useEffect } from 'react'
import ReactDOM from 'react-dom'


function AvatarPage(props) {

    let isFirstTimeUser = false


    let avt = [
        { id: 0, name: 'Rajiv', job: 'CustomerExe', gender: 'male', pic: 0 }, { id: 1, name: 'Swathi', job: 'CustomerExe', gender: 'female', pic: 1 }, { id: 2, name: 'Nakul', job: 'CustomerExe', gender: 'male', pic: 2 }, { id: 3, name: 'Preeti', job: 'CustomerExe', gender: 'female', pic: 3 }
    ]

    const [avatar, setavatar] = useState(avt)
    let avtData = []
    const avtBlock = useRef([])
    const userData = useRef(null)
    const mainDiv = useRef(null)
    const [selAvt, setselAvt] = useState(false)


    let avatars = avatar.map((x, y) => {
        return <div className="char_container" key={y} onClick={() => selectAvatar(x)}>
            <img src={'images/Character_' + x.pic + '.png'} alt={x.name} className="char_img" />
            <div className="middle" ><div ref={(e) => avtBlock.current[y] = e} style={{ display: 'none', flexFlow: 'column' }}><img src="images/Tick.png" alt="tick_icon" className="selected_icon" style={{ alignSelf: "center" }} /><button className="button" style={{ padding: "10px", alignSelf: "center" }} onClick={() => setAvatarHandler(x)}>Select</button></div></div>
            <div className="overlay"><b>{x.name}</b><br />{x.job}</div></div>


    })

    avtData.push(avatars)

    useEffect(() => {
        avtBlock.current[0].style.display = 'none'
    }, [selAvt])

    const setAvatarHandler = (x) => {
        let selectedAvatar = x;
        userData.current.style.display = 'flex'
        setselAvt(true)
        setavatar(avatar.filter(y => y == selectedAvatar))
    }

    const selectAvatar = (y) => {
        if (!selAvt) {
            avtBlock.current.forEach((e) => {
                e.style.display = 'none'
            })
            avtBlock.current[y.id].style.display = 'flex'
        }

    }

    console.log("Avatar page")
    return (
        <div id="Avatar" ref={props.avtRef} className="tabcontent" style={{ display: 'none' }}>
            <section className="tab-content">
                <div className="avatar-content">
                    <div className="char_box" ref={mainDiv}>
                        {avtData}
                        <div className="newUserData" ref={userData}>Test</div>
                        {/* <div className="char_container" id="avat_1" onClick={() => selectAvatar(this, 1)}>
                            <img src="images/character_1.png" alt="character_1" className="char_img" id="avat_img1" />
                            <div className="middle"><img src="images/Tick.png" alt="tick_icon" className="selected_icon" /></div>
                            <div className="overlay" id="avatar_title1"><b>RAJEEV CHOPRA</b><br />CUSTOMER EXECUTIVE</div>
                        </div>
                        <div className="char_container" id="avat_2" onClick={() => selectAvatar(this, 2)}>
                            <img src="images/character_3.png" alt="character_3" className="char_img" id="avat_img2" />
                            <div className="middle"><img src="images/Tick.png" alt="tick_icon" className="selected_icon" /></div>
                            <div className="overlay" id="avatar_title2"><b>SWATHI S KUMAR</b><br />CUSTOMER EXECUTIVE</div>
                        </div>
                        <div className="char_container" id="avat_3" onClick={() => selectAvatar(this, 3)}>
                            <img src="images/character_2.png" alt="character_2" className="char_img" id="avat_img3" />
                            <div className="middle"><img src="images/Tick.png" alt="tick_icon" className="selected_icon" /></div>
                            <div className="overlay" id="avatar_title3"><b>NAKUL ANAND</b><br />CUSTOMER EXECUTIVE</div>
                        </div>
                        <div className="char_container" id="avat_4" onClick={() => selectAvatar(this, 4)}>
                            <img src="images/character_3.png" alt="character_2" className="char_img" id="avat_img4" />
                            <div className="middle"><img src="images/Tick.png" alt="tick_icon" className="selected_icon" /></div>
                            <div className="overlay" id="avatar_title1"><b>SHREYA</b><br />CUSTOMER EXECUTIVE</div>
                        </div> */}
                    </div>
                </div>
            </section>
        </div>
    )
}

export default React.memo(AvatarPage)
