import React, { useEffect } from 'react'
import axios from 'axios'
import { connect } from 'react-redux'
import { retriveUserData, successUserData, errorUserData } from '../Redux'



const fetchUsers = () => {
    return (dispatch) => {
        dispatch(retriveUserData)
        axios.get('https://jsonplaceholder.typicode.com/users')
            .then(response => {
                const users = response.data
                dispatch(successUserData(users))
            })
            .catch(error => {
                const erorMsg = error.message
                dispatch(errorUserData(erorMsg))
            })
    }
}


function UserData({ userData, fechData }) {
    useEffect(() => {
        fechData()
    }, [])
    return userData.loading ? (<h2>Loading...</h2>) : userData.error ? (<h2>Something Went Wrong...</h2>) : (<div><h2>User List</h2><div>{userData && userData.users && userData.users.map(x => <p>{x.name}</p>)}</div></div>)
}

const MapStateData = (state) => {
    return {
        userData: state.user
    }

}

const MapDispatchUsers = (dispatch) => {
    return {
        fechData: () => dispatch(fetchUsers())
    }

}

export default connect(MapStateData, MapDispatchUsers)(UserData)






