import { Container, makeStyles, Typography } from '@material-ui/core'
import React from 'react'
import Carousel from './Carousel'

const useStyles = makeStyles(() =>({
    banner:{
        background:'#000 url(./images/banner2.jpg) no-repeat',
        backgroundSize:'100%'
    },
    banner_Content:{
        display:'flex',
        height:400,
        justifyContent:'space-arround',
        paddingTop:25,
        flexDirection:'column'
    },
    tagline:{
        display:'flex',
        height:'100%',
        justifyContent:'center',
        textAlign:'center',
        flexDirection:'column'
    }
}))

function Banner() {
    const classes = useStyles()
    return (
        <div className={classes.banner}>
            <Container className={classes.banner_Content}>
                <div className={classes.tagline}>
                    <Typography variant="h2" style={{fontWeight:'strong', marginBottom:15, marginTop:'2%'}}>
                        Welcome Crypto Exchange
                    </Typography>
                    <Typography style={{fontSize:'1.5vw', textAlign:'center'}}>
                        PAYMENT DASHBOARD FOR CRYPTO WHAT YOU LIKE
                    </Typography>
                </div>
                <Carousel/>
                <br/>
            </Container>
        </div>
    )
}

export default Banner
