import React, { useState } from 'react'


export default function Counter(props) {

    const value = props.counter.value

    // const [state, setstate] = useState(value)

    /*const incrementhandler = () => {
        setstate((prevCount) => {
            console.log(prevCount);
            return { value: prevCount.value + 1 }
        })
    }*/


    let classes = 'badge m-2 bg-'
    classes += value === 0 ? 'warning' : 'primary'

    let countVal = value
    countVal === 0 ? countVal = 'Zero' : countVal = value

    return (
        <div>
            <h5>ID #{props.counter.id}</h5>
            <span className={classes}><h3>{value}</h3></span>
            <button onClick={() => props.handleIncrement(props.counter)} className="btn btn-primary m-2"><h3>Increment</h3></button>
            <button onClick={() => props.onDelete(props.counter.id)} className="btn btn-danger btn-sm" type="button"><h3>Delete</h3></button>
            <br /><br />
        </div>
    )
}
