import { CircularProgress } from '@material-ui/core'
import axios from 'axios'
import { Chart as ChartJS } from 'chart.js/auto'
import React, { useContext, useEffect, useState, useRef } from 'react'
import { Line } from 'react-chartjs-2'
import { useParams } from 'react-router'
import { HistoricalChart } from '../../Config/api'
import ButtonComp from '../Components/ButtonComp'
import { Criipto } from '../Criptocontext'

function CoinInfo() {
    const { id } = useParams()
    const [loading, setloading] = useState(false)
    const [histData, sethistData] = useState([])
    const [days, setdates] = useState(1)
    const { currency } = useContext(Criipto)

    const chartDays = [
        {
            label: '24 hours',
            value: 1,
        },
        {
            label: '30 Days',
            value: 30
        },
        {
            label: '1 Year',
            value: 365
        }
    ]

    const fectchHistoricalData = async () => {
        setloading(true)
        let { data } = await axios.get(HistoricalChart(id, days, currency))
        sethistData(data.prices)
        setloading(false)
    }

    useEffect(() => {
        fectchHistoricalData()
    }, [currency, days])

    console.log("data", histData)


    return (
        <>
            <div style={{ color: '#fff' }}>
                {
                    !histData ? (<CircularProgress style={{ color: 'gold' }} />) : (<>
                        <Line
                            data={{
                                labels: histData.map((coin) => {
                                    let date = new Date(coin[0]);
                                    let time = date.getHours() > 12
                                        ? `${date.getHours() - 12}: ${date.getMinutes()} PM`
                                        : `${date.getHours()} : ${date.getMinutes()} AM`;
                                    return days === 1 ? time : date.toLocaleDateString()
                                }),
                                datasets: [{
                                    data: histData.map((coin) => coin[1]),
                                    label: `Price (Past ${days} Days) in ${currency}`,
                                    borderColor: 'gold'

                                }]
                            }}

                            options={{
                                elements: {
                                    point: {
                                        radius: 1
                                    }
                                }
                            }}
                        />
                    </>)
                }
            </div>
            <div style={{ display: 'flex', flexFlow: 'row', justifyContent: 'space-between' }}>
                {
                    chartDays.map((day) => (
                        <ButtonComp key={day.value} onClick={() => setdates(day.value)} selected={day.value == days}>{day.label}</ButtonComp>
                    ))
                }
            </div>
        </>
    )
}

export default CoinInfo
