import React, {useContext} from 'react'
import { AppBar, Container, makeStyles, MenuItem, Select, Toolbar, Typography } from '@material-ui/core'
import { useNavigate } from 'react-router-dom'
import { Criipto } from '../Criptocontext'


const useStyles = makeStyles(() => ({
    title: {
        display: 'Flex',
        flex: 1,
        color: 'Gold',
        fontFamily: 'Montserrat',
        fontWeight: 'bold',
        cursor: 'pointer'
    }
}))

export default function Header() {
    const context = useContext(Criipto)
    let navigate = useNavigate()
    const classes = useStyles()
    const {currency, setcurrency} = context
    console.log(currency)
    return (
        <AppBar  style={{background:'rgba(0, 0, 0, 0.9)'}}>
            <Container>
                <Toolbar>
                    <Typography onClick={() => navigate('/')} className={classes.title}>
                        SRINU MALLADI
                    </Typography>
                    <Select variant="outlined" value={currency} onChange={(e) => setcurrency(e.target.value)} style={{border:'1px solid #fff', color:'white'}}>
                        <MenuItem value={"USD"}>USD</MenuItem>
                        <MenuItem value={"INR"}>INR</MenuItem>
                    </Select>
                </Toolbar>
            </Container>
        </AppBar>
    )
}
