import React, { Component } from 'react'

export default class AddContact extends Component {
    state = {
        name: '',
        email: ''
    }

    add = (e) => {
        e.preventDefault();
        if (this.state.name === '' || this.state.email === '') {
            alert("Please fill all the fields")
            return
        } else {
            this.props.addContactList(this.state)
            this.setState({ name: '', email: '' })
            window.history.back()
        }
    }
    render() {
        return (
            <div className="ui main" style={{ marginTop: '50px' }}>
                <h2>Add Contact</h2>
                <div className="divider">
                    <form className="ui form" onSubmit={this.add}>
                        <div className="ui field">
                            <label>Name</label>
                            <input type="text" value={this.state.name} name="name" placeholder="Name" onChange={(e) => this.setState({ name: e.target.value })} />
                        </div>
                        <div className="ui field">
                            <label>E-mail</label>
                            <input type="text" name="email" value={this.state.email} placeholder="E-mail" onChange={(e) => this.setState({ email: e.target.value })} />
                        </div>
                        <button className="ui button blue" type='submit'>ADD</button>
                    </form>
                </div>
            </div>
        )
    }
}
