import React, { Component } from 'react'
import RenderPropB from './RenderPropB'

export class RenderPropA extends Component {
    render() {
        return (
            <div>
                <RenderPropB>
                    {(isLoggedIn) => isLoggedIn ? 'Srini' : 'Guest'}
                </RenderPropB>
            </div>
        )
    }
}

export default RenderPropA
