import React, { useState } from 'react'

function MemoComp() {
    
    const [state, setstate] = useState(0);
    const clickHandler = () => {
        setInterval(() => {
            setstate((prev) => {
                console.log(state)
                return prev + 1
            })
        }, 2000);
        
    }


    return (
        <div>
            {state}
            <button onClick={clickHandler}>Click {state}</button>
        </div>
    )
}

export default React.memo(MemoComp)
